export const registerSuccess = {
  title: "Aviso importante!",
  text: "Essa aplicao  um teste, os dados sero resetados em breve...",
  icon: "warning"
};

export function registerError({ code, error, message, validation }) {
  return code === 400 && validation.source === "running"
    ? {
        title: "Email em uso!",
        text: "Esse email j esta cadastrado no banco de dados.",
        icon: "error"
      }
    : {
        title: error,
        text: message,
        icon: "error"
      };
}

export function loginError({ code, error, message }) {
  return code === 401
    ? {
        title: "Email e/ou Senha invalido(s)!",
        text: "Verifique com ateno se o Email e Senha esto corretos.",
        icon: "error"
      }
    : code === 404
    ? {
        title: "O Email no consta no banco de dados...",
        text: "Talvez a aplicao tenha reiniciado.",
        icon: "warning"
      }
    : {
        title: error,
        text: message,
        icon: "error"
      };
}

export function removeIncidentError({ code, error, message }) {
  return code === 401
    ? {
        title: "Operao invalida!",
        text: "",
        icon: "error"
      }
    : {
        title: error,
        text: message,
        icon: "error"
      };
}

export function registerIncidentError({ code, error, message }) {
  return {
    title: error,
    text: message,
    icon: "error"
  };
}
